@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.signInOutButton {
  font-weight: 600;
  height: 2.8em;
  width: 6em;
  border: none;
  border-radius: 0.2em;
  background: rgb(48, 134, 255);
  color: white;
}

#youtubeIconContainer {
  display: flex;
  align-items: center;
}

#youtubeIcon {
  height: 2.5em;
  width: 2.5em;
}

#youTubeTextContainer {
  width: 4.5em;
}

.iconTextLetter {
  color: white;
}


#signedOutUploadPageHeader {
  display: flex;
  justify-content: center;
  margin-top: 3em;
  font-size: 3em;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(33, 33, 33, 0.98);
  height: 4em;
  width: 100vw;
  color: white;
}

#leftHeader {
  position: fixed;
  left: 1em;
  display: flex;
  align-items: center;
}

#hamburgerMenuIcon {
  font-size: 1.3em;
  margin-right: 1em;
}

/* ****************** */

#middleHeaderForm {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

/* for suggestions background  */
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px black inset;
  -webkit-text-fill-color: white;
  caret-color: white;
  color: white;
  font-size: 1.3em;
}

#searchInput {
  width: 14em;
  height: 30px;
  font-size: 1.3em;
  text-indent: 1em;
  color: white;
  background: black;
  border: none;
}

#searchInput:focus {
  outline: none;
}

#searchSubmitButton {
  height: 30px;
  width: 65px;
  border: none;
  background: rgb(77, 77, 77);
}

#searchMagIcon {
  color: white;
}

#rightHeaderContainer {
  display: flex;
  justify-content: space-around;
  position: fixed;
  right: 1em;
  display: flex;
  align-items: center;
  width: 12em;
}

#addVideoIconContainer {
  color: white;
  width: 2em;
}

#profilePicture {
  border-radius: 50%;
  height: 2em;
  width: 2em;
}

